html.if {
  min-height: 100%;
/* Prevent adjustments of font size after orientation changes in iOS. */
  -webkit-text-size-adjust: 100%;
}
body.if {
  box-sizing: border-box;
  font-family: var(--ids-font-family, If Sans, Arial, sans-serif);
  min-height: 100vh;
  line-height: var(--ids-size-line-height-28, 28px);
  color: var(--ids-color-text-primary-on-light, #331e11);
  background-color: var(--ids-color-background-100, #faf9f7);
}
*.if,
*.if::before,
*.if::after {
  line-height: var(--ids-size-line-height-28, 28px);
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.if:focus,
.if.is-focused {
  outline: 2px dotted var(--ids-color-states-focus, #0054f0);
  outline-offset: 2px;
}
.if:focus:not(:focus-visible) {
  outline: none;
  outline-offset: 0;
}
.if:focus-visible {
  outline: 2px dotted var(--ids-color-states-focus, #0054f0);
  outline-offset: 2px;
}
input.if:focus,
input.if.is-focused {
  outline: 0.125rem solid var(--ids-color-states-focus, #0054f0);
  border: 0.0625rem solid var(--ids-color-states-focus, #0054f0);
}
input.if:focus:not(:focus-visible) {
  outline: none;
  outline-offset: 0;
}
input.if:focus-visible {
  outline: 0.125rem solid var(--ids-color-states-focus, #0054f0);
  border: 0.0625rem solid var(--ids-color-states-focus, #0054f0);
}
